function sliderCabinetGallery() {
    var swiper = new Swiper('.cabinet-gallery .swiper', {
        slidesPerView: '1',
        spaceBetween: 40,
        centeredSlides: true,
        loop: true,
        navigation: {
            nextEl: '.cabinet-gallery .swiper-button-next',
            prevEl: '.cabinet-gallery .swiper-button-prev',
        },
        breakpoints: {
            320: {
                spaceBetween: 10,
            },
            760: {
                spaceBetween: 25,
            },
            1301: {
                spaceBetween: 40,
            },
        }
    })
}

function fixBtns() {
    if ($(document).height() - $(document).scrollTop() - window.innerHeight < $('footer').height()) {
        $('.fixed-btns,.fixed-btns2').css('bottom', $('footer').height() - ($(document).height() - $(document).scrollTop() - window.innerHeight) + 26);
    } else {
        $('.fixed-btns,.fixed-btns2').css('bottom', "10px");
    }
}

function fixBtnsMob() {
    if (window.innerWidth < 760) {
        if ($(document).height() - $(document).scrollTop() - window.innerHeight < $('footer').height()) {
        $('.fixed-btns__wrap').css('bottom', $('footer').height() - ($(document).height() - $(document).scrollTop() - window.innerHeight) + 26);
    } else {
        $('.fixed-btns__wrap').css('bottom', "10px");
    }
    }
}

$(window).on('scroll touchmove', function() {
    fixBtns();
    fixBtnsMob();
});

$(document).ready(function() {
    sliderCabinetGallery();
 
    //Fixed-btns
    

    fixBtns();

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})